
@media only screen and (min-width : 320px) {

  .movieHR_main{
    width: 100%;
    height: 60px;
    position: relative;
    opacity: 0;    margin-top: 5px;
  }

  .movieHR_main::after,
  .movieHR_main::before{
    content: "";
    position: absolute;
    width: 100%;
    height: 3px;
    z-index: 3;
    left: 0;
    background:-webkit-gradient(linear, left top, right top, from(rgb(105, 206, 172)), color-stop(50%, rgb(249, 51, 41)), to(rgb(235, 164, 2)));
    background:-webkit-linear-gradient(left, rgb(105, 206, 172) 0%, rgb(249, 51, 41) 50%, rgb(235, 164, 2) 100%);
    background:-o-linear-gradient(left, rgb(105, 206, 172) 0%, rgb(249, 51, 41) 50%, rgb(235, 164, 2) 100%);
    background:linear-gradient(to right, rgb(105, 206, 172) 0%, rgb(249, 51, 41) 50%, rgb(235, 164, 2) 100%);
  }
  .movieHR_main::before{
    top: -3px;
  }
  .movieHR_main::after{
    bottom: -3px;
  }
  .movie_container_hr{
    width: 16.66666%;
    height: 60px;
    float: left;
    position: relative;
    overflow: hidden;
  }
  .movie_container_hr:after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      -webkit-box-shadow: inset 0px 0px 10px 0 #000000;
              box-shadow: inset 0px 0px 10px 0 #000000;
  }

  .movie_hr_movie_image{
    width: 100%;
  }

  @-webkit-keyframes hr_movies_image_sliding{
    0%,50%,80%,100%{ -webkit-transform: translateY(0px); transform: translateY(0px) }
    10%,40%{ -webkit-transform: translateY(-93px); transform: translateY(-93px) }
  }

  @keyframes hr_movies_image_sliding{
    0%,50%,80%,100%{ -webkit-transform: translateY(0px); transform: translateY(0px) }
    10%,40%{ -webkit-transform: translateY(-93px); transform: translateY(-93px) }
  }

  .hr_image1{
    -webkit-transform: translateY(0px);
        -ms-transform: translateY(0px);
            transform: translateY(0px);
    -webkit-animation: hr_movies_image_sliding 11000ms ease-out 2s infinite;
            animation: hr_movies_image_sliding 11000ms ease-out 2s infinite;
  }
