@media only screen
and (min-width : 768px) and (max-width : 1024px)
{
  .movieHR_main_container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
  }
  .movieHR_main {
    width: 70%;
  }
  .movieHR_main{
    border-left: 3px solid;
    border-left-color:  rgb(105, 206, 172);
    border-right: 3px solid;
    border-right-color:  rgb(235, 164, 2);
  }
  @-webkit-keyframes hr_movies_image_sliding{
    0%,50%,80%,100%{ -webkit-transform: translateY(0px); transform: translateY(0px) }
    10%,40%{ -webkit-transform: translateY(-180px); transform: translateY(-180px) }
  }
  @keyframes hr_movies_image_sliding{
    0%,50%,80%,100%{ -webkit-transform: translateY(0px); transform: translateY(0px) }
    10%,40%{ -webkit-transform: translateY(-180px); transform: translateY(-180px) }
  }

}












@media only screen
and (min-width : 768px)
and (max-width : 1024px)
and (orientation: portrait) {
  .movieHR_main::after,
  .movieHR_main::before{
    width: 101.2%;
      left: -0.6%;
  }
}
@media only screen
and (min-width : 768px)
and (max-width : 1024px)
and (orientation: landscape) {
  .movieHR_main::after,
  .movieHR_main::before{
    width: 100.8%;
    left: -0.4%;
  }
  .movie_container_hr{
    width: 12.5%;
  }
}
@media only screen and (min-width : 1280px) {
  .movieHR_main::after,
  .movieHR_main::before{
    width: 100%;
    left: 0%;
  }
  .movie_container_hr{
    width: 12.5%;
  }
  .movieHR_main,.movie_container_hr{
    height: 50px;
  }
  @-webkit-keyframes hr_movies_image_sliding{
    0%,50%,80%,100%{ -webkit-transform: translateY(-20px); transform: translateY(-20px) }
    10%,40%{ -webkit-transform: translateY(-260px); transform: translateY(-260px) }
  }
  @keyframes hr_movies_image_sliding{
    0%,50%,80%,100%{ -webkit-transform: translateY(-20px); transform: translateY(-20px) }
    10%,40%{ -webkit-transform: translateY(-260px); transform: translateY(-260px) }
  }

}
